import { createStore } from "vuex";

export default createStore({
  state: {
    name: "this was only a test lmao !",
    tagsSelected: ["tools"],
    tags: ["tools", "websites", "IDE", "icon", "frameWork"],
    userToken: null,
    dataToken: null,
  },

  mutations: {
    // commit
    ARRAY_CHANGE_ADD(state, value) {
      if (state.tagsSelected.includes(value)) {
        console.log("true");
        alert("this tags was already add :)");
      } else {
        console.log("false");
        state.tagsSelected.push(value);
      }
    },
    GET_TOKEN_DATA(state, response) {
      if (response === null) {
        state.dataToken = response;
      } else {
        state.dataToken = JSON.parse(
          atob(response.data.token.token.split(".")[1])
        );
      }
    },
    REFRESH_TOKEN(state, value) {
      // eslint-disable-next-line no-undef
      state.userToken = value;
    },
  },

  actions: {},
  modules: {},
});
