<template>
  <div class="research">
    <div class="selected">
      <div
        v-for="tagSelected in tagsSelected"
        v-bind:key="tagSelected"
        class="tag close"
        v-bind:class="tagSelected"
        @click="tagsUnSelect(tagSelected)"
      >
        <h1>{{ tagSelected }}</h1>
        <div class="remove" @click="tagsSelect(tag)"></div>
      </div>
    </div>
    <input
      type="text"
      class="inputFilter"
      v-model="inputFilter"
      placeholder="research tags"
      @focus="inputFocused"
      @focusout="inputUnfocused"
    />
    <div class="tag_list focused">
      <div class="tags" v-for="tag in selectTags" v-bind:key="tag">
        <div class="tag add" v-bind:class="tag">
          <h1 @click="tagsSelect(tag)">{{ tag }}</h1>
          <div class="add" @click="tagsSelect(tag)"></div>
        </div>
      </div>
    </div>
    <!--<h1>{{ tagsSelected }}</h1>-->
  </div>
</template>
<script>
export default {
  data() {
    return {
      inputFilter: "",
    };
  },
  methods: {
    tagsSelect(value) {
      /*document.querySelector(".add." + value).classList.add("close");*/
      this.$store.commit("ARRAY_CHANGE_ADD", value);
    },
    /*inputFocused() {
      document.querySelector(".tag_list").classList.add("focused");
    },
    inputUnfocused() {
      document.querySelector(".tag_list").classList.remove("focused");
    },*/
  },
  computed: {
    tagsSelected() {
      return this.$store.state.tagsSelected;
    },
    tags() {
      return this.$store.state.tags;
    },
    selectTags() {
      return this.tags.filter((tag) => {
        return tag.match(this.inputFilter);
      });
    },
  },
  mounted() {},
};
</script>
