<template>
  <div class="nav_bar">
    <div class="mobile">
      <div @click="openMenu" id="bar" class="">
        <div class="trace"></div>
        <div class="trace"></div>
        <div class="trace"></div>
      </div>
    </div>
    <div id="element" class="">
      <nav class="nav">
        <div class="logo">LOGO</div>
        <div class="center">
          <p>
            <router-link class="link" to="/">Home</router-link>
          </p>
          <p>
            <router-link class="link" to="/Categories">Categories</router-link>
          </p>
          <p>
            <router-link class="link" to="/404">Team</router-link>
          </p>
        </div>
        <div class="right">
          <p v-if="!userToken">
            <router-link class="link" to="/SignUp">Login</router-link>
          </p>
          <div class="user" v-else>
            <p>
              <router-link to="/account">
                {{ dataToken.data.user.email }}
              </router-link>
            </p>
            <div class="account-action">
              <p @click="disconnect" class="btn">to disconnect</p>
            </div>
          </div>
        </div>
      </nav>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    userToken() {
      return this.$store.state.userToken;
    },
    dataToken() {
      return this.$store.state.dataToken;
    },
  },
  data() {
    return {
      elementBool: false,
      url_data: null,
    };
  },
  methods: {
    disconnect() {
      this.$store.commit("GET_TOKEN_DATA", null);
      this.$store.commit("REFRESH_TOKEN", null);
      setTimeout(() => this.$router.push({ path: "/SignUp" }), 1000);
    },
    openMenu() {
      let element = document.querySelector("#element");
      let effect = document.querySelector("#bar");
      if (this.elementBool === false) {
        this.elementBool = true;
        element.classList.add("open");
        effect.classList.add("open");
        console.log(this.elementBool);
      } else {
        this.elementBool = false;
        element.classList.remove("open");
        effect.classList.remove("open");
        console.log(this.elementBool);
      }
    },
  },
};
</script>

<style lang="scss">
@import "../styles/navBar";
</style>
