import card from "../components/card";

export default {
  components: {
    card,
  },
  data() {
    return {
      pathSwitch: null,
    };
  },
  created() {
    window.addEventListener("scroll", this.handleScroll);
  },
  methods: {
    switchpages() {
      this.pathSwitch = document.getElementById('swipe');
      this.pathSwitch.style.display = "flex";
      this.pathSwitch.classList.add("animate");
      setTimeout( () => this.$router.push(
        { path: '/categories'}
      ), 1000);
    },
    handleScroll() {
      let elements = document.querySelectorAll(".column");
      for (let i = 0; i < elements.length; i++) {
        elements[i].style.transform =
          "translateY(" + 1 / 3 + "px)";
      }
      let elements2 = document.querySelectorAll(".column2");
      for (let k = 0; k < elements2.length; k++) {
        elements2[k].style.transform =
          "translateY(" + (200 - 1 / 3) + "px)";
      }
    },
  },
  mounted() {
    this.handleScroll();
  },
};
