<template>
  <div class="SignUp">
    <div class="element">
      <div class="top">
        <div v-if="Login === true">
          <span>L</span>
          <span>O</span>
          <span>G</span>
          <span>I</span>
          <span>N</span>
        </div>
        <div v-else-if="Login === false">
          <span>R</span>
          <span>E</span>
          <span>G</span>
          <span>I</span>
          <span>S</span>
          <span>T</span>
          <span>E</span>
          <span>R</span>
        </div>
      </div>

      <div class="body">
        <div class="login" v-if="Login === true">
          <input
            v-model="loginEmail"
            class="input"
            type="text"
            placeholder="email"
          />
          <div class="password">
            <input
              v-model="loginPassword"
              class="input"
              type="password"
              placeholder="current-password"
            />
            <p>Forgot password?</p>
          </div>

          <div class="request">
            <!-- <button class="btn register">Register</button> -->
            <button class="btn nxt" type="button" @click="login">Login</button>
          </div>
        </div>
        <div class="register" v-else-if="Login === false">

          <input
            v-model="registerEmail"
            class="input"
            type="text"
            placeholder="email"
          />

          <input
            v-model="registerUsername"
            class="input"
            type="text"
            placeholder="username"
          />

          <input
            v-model="registerPassword"
            class="input"
            type="password"
            placeholder="password"
          />

          <input
            class="input"
            type="password"
            placeholder="confirm your password"
          />

          <div class="request">
            <button class="btn nxt" @click="register">Register</button>
          </div>
        </div>
      </div>
      <div class="bottom">
        <span class="txt" @click="Login = !Login">
          New to
          <span>LinkLib</span>
        </span>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";

export default {
  data() {
    return {
      Login: true,
      txt: null,
      /*LOGIN*/
      loginEmail: null,
      loginPassword: null,
      /*REGISTER*/
      registerUsername: null,
      registerEmail: null,
      registerPassword: null,
    };
  },
  computed: {
    test() {
      return this.$store.state.userToken;
    },
  },
  methods: {
    login() {
      let loginOptions = {
        method: "POST",
        url: "https://backlinklib.chapristi.tech/api/users/login",
        headers: {
          "Content-Type": "application/json",
        },
        data: {
          email: this.loginEmail,
          password: this.loginPassword,
        },
      };

      axios
        .request(loginOptions)
        .then((response) => {
          this.txt = response.data;
          // eslint-disable-next-line no-undef
          this.$store.commit("GET_TOKEN_DATA", response);
          this.$store.commit("REFRESH_TOKEN", response.data.token.token);
        })
        .catch(function (error) {
          console.error(error.message);
        });
    },
    register() {
      let registerOptions = {
          method: "POST",
          url: "https://backlinklib.chapristi.tech/api/users",
          headers: {
            'key': '899854d4-64d2-4d2d-8914-a15727584bce',
            'Content-Type': 'application/json',
          },
          data: {
            email: this.registerEmail,
            password: this.registerPassword,
            username: this.registerUsername,
        },
      };
      axios
        .request(registerOptions)
        .then((response) => {
          console.log(response.data);
          this.txt = response.data;
          alert('success')
        })
        .catch(function (error) {
          console.error(error);
        });
    },
  },
  mounted() {
    console.log("welcome");
  },
};
</script>
<style lang="scss">
@import "../styles/SignUp";
</style>
