  methods: {
    heart() {
      let heart = document.querySelector(".heart");
      if (this.liked == false) {
        this.liked = true;
        heart.classList.add("true");
      } else {
        this.liked = false;
        heart.classList.remove("true");
      }
    },
    star() {
      let star = document.querySelector(".star");
      if (this.upvote == false) {
        this.upvote = true;
        star.classList.add("true");
      } else {
        this.upvote = false;
        star.classList.remove("true");
      }
